import { useReducer } from "react";
import Header from "../components/Header";
import ServiceItem from "../components/ServiceItem";
import {
  AboutTitle,
  AboutTitleDiv,
  AboutTitleIcon,
  BackgroundImage,
  DivSides,
  DivWrapper,
  InputContainer,
  MappedDivWrapper,
  NumberInput,
  ParagraphAbout,
  ParagraphWrapper,
  Sider,
  Span,
} from "../components/StyledComponent";

import imgService from "../assets/406068280_304729212527505_1161707641134475858_n.jpg";
import { useEffect } from "react";

const initialState = {
  ServiceArray: [
    { text: " ", id: 1 },
    { text: "  ()", id: 2 },
    { text: " () ", id: 3 },
    { text: "  ", id: 4 },
    { text: " ", id: 5 },
    { text: "   ", id: 6 },
  ],
  CheckedItems: [],
  inputValue: 0,
  servicePrice: "",
};

function reducer(state, action) {
  switch (action.type) {
    case "service/filter":
      return {
        ...state,
        CheckedItems: [...action.payload],
      };
    case "service/inputValue":
      return {
        ...state,
        inputValue: action.payload,
      };
    case "service/setPrice":
      return {
        ...state,
        servicePrice: action.payload,
      };
    default:
      return state;
  }
}

function ServicePage() {
  const [{ ServiceArray, CheckedItems, inputValue, servicePrice }, dispatch] =
    useReducer(reducer, initialState);

  useEffect(() => {
    function setPrice() {
      const price = 2.5 * CheckedItems.length * Number(inputValue);
      dispatch({ type: "service/setPrice", payload: price });
    }
    setPrice();
  }, [CheckedItems.length, inputValue]);

  console.log(ServiceArray);

  return (
    <>
      <Header margin={"0"} />
      <DivWrapper>
        <AboutTitleDiv>
          <AboutTitleIcon />
          <AboutTitle></AboutTitle>
        </AboutTitleDiv>
        <ParagraphWrapper>
          <ParagraphAbout>
            <Span>-</Span> ,    
            ,      
            
          </ParagraphAbout>
          <ParagraphAbout>
            <Span>-</Span>-  100%-  
              " ".  
             11    3,000-  
            .  ,   , 
             ,    
            ,
          </ParagraphAbout>
          <ParagraphAbout>
            <Span>-</Span>-    
            . 2019      
            .    m ,  2000
             .
          </ParagraphAbout>
          <ParagraphAbout>
            <Span>-</Span>-   : m
            , m , m , m , m
            ,  ,  , m  2, m
             2, m , m .  
            m-    : m Highlight, m ,
            m , m , m  2, m 
            .
          </ParagraphAbout>
          <ParagraphAbout>
            <Span>-</Span> -   !
          </ParagraphAbout>
        </ParagraphWrapper>
        <DivSides>
          <Sider>
            <AboutTitleDiv>
              <AboutTitleIcon />
              <AboutTitle>  </AboutTitle>
            </AboutTitleDiv>
            <MappedDivWrapper>
              {ServiceArray.map((item) => (
                <ServiceItem
                  service={item.text}
                  key={item.id}
                  CheckedItems={CheckedItems}
                  id={item.id}
                  dispatch={dispatch}
                />
              ))}
              <InputContainer>
                <AboutTitleDiv>
                  <AboutTitleIcon />
                  <AboutTitle>  </AboutTitle>
                </AboutTitleDiv>
                <NumberInput
                  type="number"
                  value={inputValue}
                  onChange={(e) =>
                    dispatch({
                      type: "service/inputValue",
                      payload: e.target.value,
                    })
                  }
                />
              </InputContainer>
              <AboutTitle>  : {servicePrice} </AboutTitle>
            </MappedDivWrapper>
          </Sider>
          <BackgroundImage image={imgService} />
        </DivSides>
      </DivWrapper>
    </>
  );
}

export default ServicePage;
